* {
  margin: 0;
  padding: 0;
  border: 0;
}

.mainClass {
  margin: 0px;
  background-color: rgb(57, 57, 57);
  color: #ffffff;
  width: 100vw;
  border-bottom: 1px solid #0c7b93;
  .titleText {
    width: 100vw;
    height: 10%;
    line-height: 70%;
    margin-left: 1%;
    padding-top: 2%;
    h1 {
      font-family: 'Lato', sans-serif;
      font-size: 3em;
      color: #00d0ff;
    }
    h2 {
      font-family: 'Cookie', cursive;
      font-size: 2.4em;
      text-align: left;
      padding-top: 2.3%;
      color: #ffffff;
    }
  }
  .personalBranding {
    padding-top: 1%;
    padding-bottom: 2%;
    padding-left: 5%;
    text-align: justify;
    width: 90%;
  }
  .personalSkills {
    padding-top: 1%;
    padding-bottom: 2%;
    padding-left: 22%;
    text-align: justify;
    width: 56%;
  }
}

.carousel {
  padding-top: 4%;
  padding-bottom: 1%;
  padding-left: 2.5%;
  text-align: center;
  width: 95%;
}

footer {
  display: flex;
  justify-content: space-evenly;
  background-color: black;
  height: 7vh;
  padding-top: 1%;
  a {
    font-size: 33px;
    text-decoration: none;
    color: rgb(0, 217, 255);
  }
}

.flex {
  display: flex;
  text-align: justify;
  justify-content: space-around;
  .flexItem {
    max-width: 30%;
  }
}

h2 {
  font-family: 'Cookie', cursive;
  color: #00d0ff;
  font-size: 2.2em;
  text-align: center;
  padding-bottom: 1%;
  padding-top: 2%;
}

h3,
h4 {
  font-family: 'Lato', sans-serif;
  color: #00d0ff;
}

p {
  font-family: 'Lato', sans-serif;
}

img {
  width: 35vw;
  border: 1px solid #00d0ff;
}
